@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;500;700&display=swap");
:root {
  --color-primary: #f4c22f;
  --color-text-main: #676767;
}

[class*=__container] {
  margin: 0 auto;
  padding: 0 0.9375rem;
  max-width: 106.875rem;
}

.locked {
  overflow: hidden;
  touch-action: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);
}

* {
  padding: 0;
  margin: 0;
  border: 0;
}
*, *:before, *:after {
  box-sizing: border-box;
}

aside,
nav,
footer,
header,
section {
  display: block;
}

html,
body {
  width: 100%;
  height: 100%;
  min-width: 320px;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  color: var(--color-text-main);
  font-size: 16px;
  line-height: 1.5;
  font-family: "Open Sans", sans-serif;
  text-size-adjust: 100%;
}

input,
button,
textarea {
  font-size: inherit;
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
  user-select: none;
}
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a {
  color: inherit;
  text-decoration: none;
  user-select: none;
}
a:visited {
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}

ul,
li {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle;
  font-style: italic;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
}

sup {
  color: inherit;
}

.helper {
  position: fixed;
  left: calc(100% - 25px);
  top: 50%;
  transform: rotate(-90deg);
  transform-origin: 0% 0%;
  width: 155px;
  height: 25px;
  color: #fff;
  font-size: 12px;
  background-color: var(--color-primary);
  border-top-right-radius: 5px;
  border-top-left-radius: 5px;
}
.helper span {
  margin-left: 3px;
  font-size: 14px;
}

.header {
  flex-shrink: 0;
  background-color: #fff;
}
.header__container {
  position: relative;
}
.header__logo {
  margin-right: 60px;
}
.header__burger {
  display: none;
}
@media (max-width: 47.99875em) {
  .header__burger {
    display: block;
    position: relative;
    width: 30px;
    height: 23px;
    background-color: transparent;
    cursor: pointer;
    z-index: 5;
  }
  .header__burger::before, .header__burger::after {
    content: "";
  }
  .header__burger span, .header__burger::before, .header__burger::after {
    position: absolute;
    right: 0;
    width: 100%;
    height: 3px;
    background-color: #fff;
    border-radius: 2px;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  }
  .header__burger span {
    transition-delay: 150ms;
    top: calc(50% - 1px);
  }
  .header__burger::before {
    top: 0;
    transition: top 150ms 150ms, transform 150ms;
  }
  .header__burger::after {
    bottom: 0;
    transition: bottom 150ms 150ms, transform 150ms;
  }
  .header__burger--active span {
    opacity: 0;
  }
  .header__burger--active::before {
    top: calc(50% - 2px);
    transform: rotate(-45deg);
    transition: top 150ms, transform 150ms 150ms;
  }
  .header__burger--active::after {
    bottom: calc(50% - 2px);
    transform: rotate(45deg);
    transition: bottom 150ms, transform 150ms 150ms;
  }
}

.main-nav {
  background-color: #f6f6f6;
}
.main-nav ul {
  display: flex;
  margin-left: auto;
  width: fit-content;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  overflow: hidden;
}
.main-nav ul li:not(:last-child) {
  border-right: 1px solid #fff;
}
.main-nav__link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 164px;
  height: 35px;
  text-transform: uppercase;
  color: #fff;
  background-color: #b8b8b8;
  transition: opacity 300ms cubic-bezier(0.4, 0, 0.2, 1);
}
.main-nav__link--current {
  background-color: var(--color-primary);
  pointer-events: none;
}
.main-nav__link:hover {
  opacity: 0.7;
}

.menu {
  display: flex;
  align-items: center;
}
@media (max-width: 47.99875em) {
  .menu {
    position: fixed;
    top: 0;
    left: 100%;
    display: flex;
    row-gap: 40px;
    flex-direction: column;
    padding: 110px 15px 50px 15px;
    width: 100%;
    height: 100%;
    background-color: #1e6091;
    transition: left 300ms cubic-bezier(0.4, 0, 0.2, 1);
    overflow: auto;
  }
  .menu::before {
    content: "";
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 52px;
    background-color: var(--color-primary);
  }
  .menu--active {
    left: 0;
  }
}
.menu__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 15px;
  padding-top: 11px;
  padding-bottom: 11px;
  min-height: 114px;
}
.menu__list {
  display: flex;
  gap: 43px;
}
@media (max-width: 47.99875em) {
  .menu__list {
    flex: 1 1 auto;
    flex-direction: column;
    align-items: flex-end;
    font-size: 32px;
  }
}
.menu__link {
  display: flex;
  color: #676767;
  font-weight: 600;
  text-transform: uppercase;
  transition: color 300ms cubic-bezier(0.4, 0, 0.2, 1);
}
.menu__link:hover:not(.menu__link--current) {
  color: var(--color-primary);
}

.header-control {
  display: flex;
  align-items: center;
}
.header-control__label {
  display: block;
  position: relative;
  margin-right: 28px;
  max-width: 240px;
}
.header-control__label svg {
  position: absolute;
  top: 50%;
  left: 19px;
  transform: translateY(-50%);
}
.header-control__input {
  padding-left: 43px;
  padding-right: 20px;
  width: 100%;
  height: 50px;
  border: 1px solid #b8b8b8;
  border-radius: 100px;
  outline: 2px solid transparent;
  transition: outline-color 300ms cubic-bezier(0.4, 0, 0.2, 1);
}
.header-control__input:focus {
  outline-color: var(--color-primary);
}
.header-control__input::placeholder {
  opacity: 0.8;
}
.header-control__button {
  display: flex;
  align-items: center;
  gap: 9px;
  color: #7e7e7e;
  background: none;
}
.header-control__button--cart {
  margin-right: 34px;
}
.header-control__button--cart svg {
  color: #7e7e7e;
}

.hero__container {
  padding-bottom: 29px;
  max-width: 1920px;
}

.hero-swiper__slide {
  position: relative;
  display: flex;
  justify-content: flex-end;
  overflow: hidden;
}
.hero-swiper__slide > img {
  position: absolute;
  top: 57px;
  left: -30px;
  width: 100%;
  max-width: 919px;
  z-index: 1;
}
.hero-swiper__content {
  display: flex;
  width: 100%;
  max-width: 1112px;
  height: 720px;
  color: #676767;
  background-color: #f5f5f5;
  background-image: url("../images/Home/sugar.png");
  background-repeat: no-repeat;
  background-position: top 45px right 0;
}
.hero-swiper__content h2 {
  margin-bottom: 15px;
  max-width: 317px;
  font-size: 96px;
  font-weight: 700;
  line-height: 1.1;
}
.hero-swiper__content p {
  margin-bottom: 33px;
  max-width: 440px;
}
.hero-swiper__details {
  margin: 107px 0 0 165px;
}
.hero-swiper__button {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 12px;
  width: 252px;
  height: 50px;
  color: #fff;
  background-color: var(--color-primary);
  border-radius: 5px;
  text-transform: uppercase;
}

.hero-swiper-pagination {
  bottom: 78px !important;
  left: 32px !important;
}
.hero-swiper-pagination .swiper-pagination-bullet {
  width: 11px;
  height: 11px;
  background-color: transparent;
  border: 1px solid var(--color-primary);
  opacity: 1;
}
.hero-swiper-pagination .swiper-pagination-bullet-active {
  background-color: var(--color-primary);
}

.products {
  background-color: #f8f8f8;
}

.swiper-prod {
  padding-top: 72px;
  padding-bottom: 142px;
}

.swiper-prod-button-prev,
.swiper-prod-button-next {
  width: fit-content;
  height: fit-content;
}

.swiper-prod-button-prev {
  left: -76px;
}
.swiper-prod-button-prev svg {
  rotate: 180deg;
}

.swiper-prod-button-next {
  right: -76px;
}

.swiper-button-prev:after,
.swiper-button-next:after {
  content: none;
}

.product {
  position: relative;
  max-width: 386px;
  font-size: 14px;
  background-color: #fff;
  border-radius: 5px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  overflow: hidden;
}
.product:not(:last-child) {
  margin-right: 45px;
}
.product__sale {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 78px;
  height: 39px;
  font-size: 18px;
  font-weight: 600;
  color: #fff;
  background-color: #63b569;
  border-bottom-right-radius: 5px;
}
.product__favorite {
  position: absolute;
  top: 25px;
  right: 22px;
  display: flex;
  background: none;
}
.product > img {
  display: block;
  margin-inline: auto;
  margin-block: 38px;
  width: 100%;
  height: 100%;
  max-height: 236px;
  object-fit: contain;
}
.product__content {
  padding-inline: 28px;
  padding-bottom: 23px;
}
.product__title {
  margin-bottom: 4px;
  text-transform: uppercase;
  color: #676767;
  font-weight: 600;
}
.product__aftertitle, .product__rating, .product__control {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.product__aftertitle {
  margin-bottom: 8px;
}
.product__aftertitle h4 {
  color: #000;
  text-transform: uppercase;
}
.product__aftertitle p {
  text-decoration: line-through;
}
.product__rating {
  margin-bottom: 25px;
}
.product__rating div {
  display: flex;
  align-items: center;
  gap: 10px;
}
.product__rating p {
  font-size: 18px;
  font-weight: 600;
}
.product__control {
  text-transform: uppercase;
}
.product__control div > p:first-child {
  margin-bottom: 5px;
  font-weight: 600;
  color: #63b569;
}
.product__control button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 50px;
  border: 1px solid var(--color-primary);
  border-radius: 6px;
  background-color: #fff;
}
.product__control button svg {
  color: var(--color-primary);
}

.tabs {
  width: 100%;
}
.tabs__header {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
@media (max-width: 768px) {
  .tabs__header {
    flex-wrap: wrap;
  }
}
.tabs__header-item {
  padding: 20px;
  background-color: #e8e8e8;
  text-align: center;
  cursor: pointer;
}
.tabs__header-item--active {
  font-weight: 600;
  background-color: #f8f8f8;
}
.tabs__content-item {
  position: relative;
}
.tabs__content-item:last-of-type {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 705px;
  font-size: 120px;
  font-weight: 700;
  background-color: var(--color-primary);
}

.footer {
  margin-top: auto;
  background-color: #fcfcfc;
}
.footer__title {
  position: relative;
  margin-bottom: 30px;
  width: fit-content;
  font-size: 20px;
  font-weight: 600;
  text-transform: uppercase;
}
.footer__title::after {
  content: "";
  display: block;
  position: absolute;
  bottom: -3px;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: var(--color-primary);
}
.footer__link:hover {
  text-decoration: underline;
  text-underline-offset: 3px;
}

.footer-contact {
  background-color: #fff;
}
.footer-contact__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 136px;
}
.footer-contact__socials {
  display: flex;
  gap: 30px;
}
.footer-contact__questions {
  display: flex;
}
.footer-contact__questions h4 {
  margin-right: 68px;
  text-decoration: underline;
  text-decoration-color: #ccc;
}
.footer-contact__questions ul {
  display: flex;
  gap: 55px;
}
.footer-contact__questions ul a {
  display: flex;
  align-items: center;
  gap: 15px;
}

.footer-content {
  display: flex;
  justify-content: space-between;
  padding-top: 38px;
  padding-bottom: 55px;
}
.footer-content__links {
  display: flex;
  justify-content: space-between;
  gap: 20px;
  width: 100%;
  max-width: 854px;
}
.footer-content__list {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.footer-content__address {
  display: flex;
  align-items: start;
  gap: 82px;
}
.footer-content__address address {
  width: 100%;
  font-style: normal;
}
.footer-content__address img {
  width: 100%;
  max-width: 330px;
  height: 100%;
  max-height: 206px;
}
.footer-content__address-item:first-child {
  padding-bottom: 30px;
  border-bottom: 2px solid var(--color-primary);
}
.footer-content__address-item:first-child a {
  display: flex;
  max-width: 130px;
}
.footer-content__address-item:last-child {
  padding-top: 30px;
}
.footer-content__address-item h4 {
  margin-bottom: 13px;
  font-weight: 600;
}

.footer-copyright {
  border-top: 1px solid #d9d9d9;
  font-size: 14px;
}
.footer-copyright a {
  display: flex;
}
.footer-copyright__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 50px;
}
.footer-copyright__links {
  display: flex;
  gap: 65px;
}
.footer-copyright__companies {
  display: flex;
  align-items: center;
  gap: 24px;
}